	<div class="header">
		<video src="../images/lym_a.mp4" id="myVideo" ref="vid" muted="true" class="header-video" autoplay="autoplay" loop="loop" x-webkit-airplay="true" style="width: 1920px;" webkit-playsinline="true" playsinline="true" preload="true">
