<script>
	import "../css/reset.css";


	export default {
		components: {
		},
		data() {
			return {
//				tab: 1, // 默认为0
//				listTab: 0,// 荣耀之路的插槽tab
//				hostList: [], //荣耀金主
//				groupList: [], //荣耀公会
//				unTime: [], // 用于时间戳判断是否允许点击之后的进程,
			}
		},
		methods: {
			//跳房间页
//			goItem(domain) {
//				if (domain) {
//					window.open(`//star.longzhu.com/${domain}`, '_blank')
//				}
//			},

			watchVideoPlay() {
				var vi = document.getElementById("myVideo");
				var promise = document.getElementById('myVideo').play();
				vi.muted = true
				if (promise !== undefined) {
					promise.then(() => {
						vi.play();
						console.log('加载视频成功')
					}).catch(error => {
						console.log('加载视频错误 ->', error)
						vi.pause();
						setTimeout(()=>{
							this.watchVideoPlay()
							console.log('进入定时,2s重新调取视频')
						},2000)
						// Autoplay was prevented.
						// Show a "Play" button so that user can start playback.
					});
				}
			}
		},
		mounted() {

			this.$nextTick(()=> {
				document.getElementById("myVideo").muted = true
				this.watchVideoPlay() //解决 首次加载不渲染的问题 并加入失败处理,
			})

//      测试数据
